import React from "react";
import {NavLink} from "react-router-dom";
import "./Filter_button.css";
// import down_icon from "..image/angle-down-solid.svg";
// import up_icon from "..image/angle-up-solid.svg";

const Filter_button =(props)=>{
    return(
        <>
            <div className="col-12 col-sm-6 col-xl-3 my-2  text-center">

            <div className="dropdown drop-button-section ">
                <button className="btn  drop-button dropdown-toggle " type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                {props.name}
                </button>
                
                <div className="dropdown-menu " aria-labelledby="dropdownMenuButton">
                    <NavLink className="dropdown-item" to="#">{props.ex1}</NavLink>
                    <NavLink className="dropdown-item" to="#">{props.ex2}</NavLink>
                    <NavLink className="dropdown-item" to="#">{props.ex3}</NavLink>
                </div>
            </div>
            </div>
            
                        
        </>
    )
}

export default Filter_button;