import React,{useState} from "react";
// import img1 from "../image/one.png";
// import img2 from "../image/two.png";
// import img3 from "../image/three.png";
// import img4 from "../image/four.png";
import img1 from "../image/pone.png";
import img2 from "../image/ptwo.png";
import img3 from "../image/pthree.png";
import img4 from "../image/pfour.png";


import {NavLink} from "react-router-dom";
import Button from "./Button";
import "./Pop_window.css";

const Pop_window=()=>{
    const [currentValue, setValue]=useState(0);
    const incNum =()=>{
        setValue(currentValue + 1)
    };
    const decNum =()=>{
        if(currentValue>1){
        setValue(currentValue -1)}
    };

    
    
    const [currentImg, setImg]=useState(img1);
    
    const One =()=>{
        setImg(img1);
        
    };
    const Two =()=>{
        setImg(img2);
    };
    const Three =()=>{
        setImg(img3);
    };
    const Four =()=>{
        setImg(img4);
        console.log(currentImg);

    };
    console.log(
        currentImg+" image name"
    );
    return(
        
        <>
        
            <section id="popupwidow" className="popupwindow">
            <div class="modal  fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered  ">
                <div class="modal-content d-flex justify-content-center ">

                <div class="modal-body bg-light ">
                <div className="container " >
                        <div className="row ">
                            <div className="col ">
                                <div className="row d-flex justify-content-between">
                                    <div className="col-5 px-0 text-center">
                                        <img src={currentImg} id="main_photo"  width="370" height="370"></img>
                                        <ul class=" d-flex flex-row text-center">
                                            <img src={img1} className="small_photo" width="70" height="50"  onClick={One}></img> 
                                            <img src={img2} className="small_photo" width="70" height="50"  onClick={Two}></img> 
                                            <img src={img3} className="small_photo" width="70" height="50"  onClick={Three}></img> 
                                            <img src={img4} className="small_photo" width="70" height="50"  onClick={Four}></img> 
                                            
                                        </ul>
                                        {
                                               function  myFunction(imgg)
                                               {
                                                   var Fullimg = document.getElementById("main_photo");
                                                    Fullimg.src = imgg.src;
                                               }
                                        }
                                    </div>
                                    <div className="col-6">
                                        <h1 className="mt-5">Raxon XII Product</h1>
                                        <NavLink to=""><i class="fa fa-star pf-star-icon" aria-hidden="true"></i></NavLink>
                                        <NavLink to=""><i class="fa fa-star pf-star-icon" aria-hidden="true"></i></NavLink>
                                        <NavLink to=""><i class="fa fa-star pf-star-icon" aria-hidden="true"></i></NavLink>
                                        <NavLink to=""><i class="fa fa-star pf-star-icon" aria-hidden="true"></i></NavLink>
                                        <NavLink to=""><i class="fa fa-star pf-star-icon" aria-hidden="true"></i></NavLink>
                                        <h3>Short Descriptions</h3>
                                        <p className="descriptions">Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since when an unknown printer took a galley of type and scrambled it to make a type specimen book.</p>
                                        <div className="pricing">
                                            <p>Price:&nbsp;<span>$25.10</span></p>
                                            <p>Regular Price:&nbsp;<span>$25.10</span><span>&nbsp;&nbsp;( 10% Off )</span></p>
                                            <p>Shipping:&nbsp;<span>$00</span></p>
                                        </div>
                                        <div class="btn-group mt-3 " role="group" aria-label="Basic example">
                                            <p className="quantity pt-2">Quantity</p>
                                            <div className="btn_group ml-3 ">
                                                <button type="button" class="btn btn_minus  rounded-0" onClick={decNum} >-</button>
                                                <input type="text" value={currentValue} id="quantity"  class="btn  btn_view"/>
                                                <button type="button" class="btn btn_plus  rounded-0 " onClick={incNum}>+</button>
                                            </div>
                                            
                                                
                                        </div>
                                        <div>
                                            {<Button link="" name="Add to List"/>}
                                            {<Button link="" name="Buy Now"/>}
                                        </div> 
                                        

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                </div>
                     
                </div>
            </div>
            </section>
        </>
    )
}

export default Pop_window;