import React from "react";
import {NavLink} from "react-router-dom";
import "./Footer.css";

const Footer=()=>{
    return(
        <>
            <section id="Footer-section">
                <div className="container-fluid footer">
                    <div className="container pt-5">
                        <div className="row">
                            <div className="col- col-md-6">
                                <p className="footer-p text-center text-md-left">
                                    Copyright 2017 - All Right Reserved
                                </p>
                            </div>
                            <div className="col- col-md-6">
                            <ul className="list-inline footer-ul text-center text-md-right">
                                <li className="list-inline-item ">
                                    <NavLink className=" footer-nav-link" to="#">Help</NavLink>
                                </li>
                                <li className="list-inline-item">
                                    <NavLink className="footer-nav-link" to="#">About</NavLink>
                                </li>
                                <li className="list-inline-item">
                                    <NavLink className="footer-nav-link" to="#">FAQ</NavLink>
                                </li>
                                <li className="list-inline-item">
                                    <NavLink className="footer-nav-link" to="#">Privacy</NavLink>
                                </li>
                                <li className="list-inline-item">
                                    <NavLink className="footer-nav-link" to="#">Contact</NavLink>
                                </li>
                                <li className="list-inline-item">
                                    <NavLink className="footer-nav-link" to="">Term</NavLink>
                                </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                
            </section>
        </>
    )
}

export default Footer;