import React from "react";
import "./Header.css";
import {NavLink} from "react-router-dom";

const Header =()=>{
    return(
        <>
            <section id="header-section" className="">
            <div className="container-fluid header ">
                <div className="container ">
                    <div className="row header-row ">
                        <div className="col d-flex  justify-content-center flex-column align-items-center">
                            <h1 className="header-h1 pb-5">Online shopping</h1>
                            <p className="header-p">Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem ipsum has
                                been the industry when an unknown printer took a gallery of type and scrambled.
                            </p>
                            <form className="form search-section">
                                <NavLink to="" className="btn search-button" type="submit"><i class="fa fa-search" aria-hidden="true"></i></NavLink>
                                <input className="form-control search-bar pl-4 pr-5 border-0" type="text" placeholder="Search.." aria-label="Search"/>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            </section>
        </>
    )
}

export default Header;