import React from 'react';
import {NavLink} from "react-router-dom";
import './Navbar.css';

const Navbar = () => {
    return (
        <>
        <div className="container-fluid nav-section">
            <div className="container">
                <div className="row">
                    <div className="col">
                        <nav className="navbar navbar-expand-lg navbar-light ">
                            <NavLink className="navbar-brand" to="#">SaaSlab</NavLink>
                            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>

                            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                <ul className="navbar-nav d-flex justify-content-center align-items-start  ml-lg-auto">
                                <li className="nav-item text-center active">
                                    <NavLink className="nav-link top-nav-link" to="#">Home</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link top-nav-link" to="#">Product</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link top-nav-link" to="#">Featured</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link top-nav-link" to="#">FAQ</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link top-nav-link" to="#">Contact</NavLink>
                                </li>
                                <li>
                                    <NavLink className="nav-link top-nav-link" to=""><i class="fa fa-shopping-cart" aria-hidden="true"></i><span class="badge  ">4</span></NavLink>
                                </li>
                                </ul>
                            </div>
                        </nav>
                    </div>
                </div>
            </div>
            
        </div>
        </>
    )
}

export default Navbar;
