import React from "react";
import { NavLink } from "react-router-dom";
import "./Product_Card.css";
const Recent_view_card=(Props)=>{
    return(
        <>
        
        <div className="item">
                <div className="card  ">
                    <img src={Props.Product_image} className="card-img-top" alt={Props.Product_image}/>
                    <div className="card-body">
                        <div className="row">
                            <div className="col-9">
                                <h5 className="card-title Product_name h-auto mb-1 ">{Props.Product_name}</h5>
                                <p className="card-text Product_brand text-left text-muted h-auto mb-1">{Props.Product_brand}</p>
                                <p className="card-text Product_price text-left h-auto"><small className="text-muted">Price: <strong>${Props.Product_price}</strong></small></p>
                            </div>
                            <div className="col-3">
                                 <NavLink to=""><i class="fa fa-heart text-muted p-heart-icon" aria-hidden="true"></i></NavLink>
                                 <NavLink to=""><i class="fa fa-share-alt mt-3 text-muted p-share-icon" aria-hidden="true"></i></NavLink>
                            </div>
                        </div>
                    </div>
            </div>
            
        </div>    
        </>
    )
}

export default Recent_view_card;