import React, { Component } from "react";
import {NavLink} from  "react-router-dom";
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import Recent_view_data from "./Recent_view_data";
import Recent_view_card from "./Recent_view_card";
import "./Recently_viewed.css";
import img11 from "../image/one.png";


class Reactly_viewed extends Component{
    state= {
        responsive:{
            0: {
                items: 1,
            },
            769: {
                items: 2,
            },
            1000: {
                items: 3,
            },
        },
    }
    render(){
        return(
        <>
           <section id="Recent-views-section">
               <div className="container-fluid Recent-view-container">
                    <div className="container Recent-view-container2 "> 
                            <div className="row ">
                                <div className="col ">
                                    <h1 className="Recent-view-h1">Recently You Have Viewed</h1>
                                    <p className="Recent-view-p mx-auto">Lorem,ipsum dolor slt amet,consectetur adplislicng elusmod tempor incididunt ut labore et consectetur adlplslcing elusmid dolore magna allqua.</p>
                                </div>
                            </div>
                        <div className="row Recent-view-container-row ">
                            <div className="col px-5 ">
                                <div className="row d-flex justify-content-center align-items-center">
                                    <div className="col-10 px-0 px-md-4 ">

                                        <OwlCarousel 
                                        className="owl-theme"
                                        
                                        margin={30}
                                        autoplay
                                        nav
                                        loop
                                        navText ={  ["<i class='fas fa-long-arrow-alt-left'></i>","<i class='fas fa-long-arrow-alt-right'></i>"]}
                                        responsive={this.state.responsive}
 
                                        
                                        >
                                            
                                                {
                                                    Recent_view_data.map((pval, pind)=>{
                                                        return <Recent_view_card key={pind} 
                                                        Product_image={pval.Product_image}
                                                        Product_name={pval.Product_name}
                                                        Product_brand={pval.Product_brand}
                                                        Product_price={pval.Product_price}
                                                        />
                                                    })
                                                }
                                            
                                            
                                        </OwlCarousel>
                                                          
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
               </div>
           </section> 
        </>
    )}
}

export default Reactly_viewed;