import React from "react";
import {NavLink} from  "react-router-dom";
import Filter_button from "./Filter_button";
import Product_data from "./Product_data";
import Product_card from "./Product_Card";
import "./Product.css";

const Product=()=>{
    return(
        <>
            <section id="Product-section" className="Product-section">
                <div className="container">
                    <div className="row my-4">
                            {<Filter_button name="Select Product" ex1="example1" ex2="example2" ex3="example3"/>}
                            {<Filter_button name="Select Catagory" ex1="example1" ex2="example2" ex3="example3"/>}
                            {<Filter_button name="Select Brand" ex1="example1" ex2="example2" ex3="example3"/>}
                            {<Filter_button name="Price Rank" ex1="example1" ex2="example2" ex3="example3"/>}
                        
                    </div>
                    <div className="row">
                        {
                            Product_data.map((val, ind)=>{
                                return <Product_card key={ind} 
                                Product_image={val.Product_image}
                                Product_name={val.Product_name}
                                Product_brand={val.Product_brand}
                                Product_price={val.Product_price}
                                />
                            })
                        }
                    </div>
                    <div className="row">
                        <div className="col d-flex justify-content-center mt-md-5 ">
                            <nav aria-label="Page navigation example">
                                <ul class="pagination pagination-sm">
                                    <li class="page-item">
                                    <NavLink class="page-link" to="#" aria-label="Previous">
                                        <i class="fa fa-chevron-left" aria-hidden="true"></i>
                                    </NavLink>
                                    </li>
                                    <li class="page-item"><NavLink class="page-link" to="#">1</NavLink></li>
                                    <li class="page-item"><NavLink class="page-link" to="#">2</NavLink></li>
                                    <li class="page-item"><NavLink class="page-link" to="#">3</NavLink></li>
                                    <li class="page-item"><NavLink class="page-link" to="#">4</NavLink></li>

                                    <li class="page-item">
                                    <NavLink class="page-link" to="#" aria-label="Next">
                                        <i class="fa fa-chevron-right" aria-hidden="true"></i>
                                    </NavLink>
                                    </li>
                                </ul>
                            </nav>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Product;