import React from "react";
import Button from "./Button";
import {NavLink} from "react-router-dom";
import "./Featured_card.css";

const Featured_card =(props)=>{
    return(
            <>
                <div className="col-12">
                            <div className="card mb-3 border-0">
                                <div className="row  bg-light">
                                    <div className="col-md-3 mx-auto text-center">
                                    <img src={props.Product_image} alt={props.Product_image} className=" w-100 h-100 text-center"/>
                                    </div>
                                    <div className="col-md-9">
                                    <div className="card-body pf-card-body">
                                        <div className="row">
                                            <div className="col-8 text-left">
                                                <h5 className="card-text">{props.Product_name}</h5>
                                            </div>
                                            <div className="col">
                                                <h5 className="text-right">${props.Product_price}</h5>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col">
                                                <p className="card-text">{props.Product_detail}</p>
                                                <NavLink to=""><i class="fa fa-star pf-star-icon" aria-hidden="true"></i></NavLink>
                                                <NavLink to=""><i class="fa fa-star pf-star-icon" aria-hidden="true"></i></NavLink>
                                                <NavLink to=""><i class="fa fa-star pf-star-icon" aria-hidden="true"></i></NavLink>
                                                <NavLink to=""><i class="fa fa-star pf-star-icon" aria-hidden="true"></i></NavLink>
                                                <NavLink to=""><i class="fa fa-star pf-star-icon" aria-hidden="true"></i></NavLink>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-12 col-md-12 col-lg-4">
                                                <NavLink to=""><i class="fa fa-heart text-muted pf-heart-icon mr-4" aria-hidden="true"><span className="ml-1">3k</span></i></NavLink>
                                                <span>I</span>
                                                <NavLink to=""><i class="fa fa-share-alt mt-3 text-muted pf-share-icon ml-4" aria-hidden="true"><span className="ml-1">45k</span></i></NavLink>
                                            </div>
                                            <div className="col-12  col-md-12 col-lg-8 text-center text-md-center text-lg-right">
                                                <Button link="" name="View Product"/>
                                                <Button link="" name="Add to Card"/>
                                            </div>
                                        </div>
                                        
                                    </div>
                                    </div>
                                </div>
                            </div>

                        </div>
            </>
    )
}

export default Featured_card;