import React from "react";
import {NavLink} from "react-router-dom";
import "./Featured.css";
import Product_feature_data from "./Product_feature_data";
import Featured_card from "./Featured_card";

const Featured=()=>{
    return(
        <>
            <section id="Feature-section">
                <div className="container-fluid feature-container ">
                    <div className="container pt-5">
                        <div classNamel="row">
                            <div className="col">
                                <h1 className="feature-h1">Weekly Featured Product</h1>
                                <p className="feature-p mx-auto">Lorem ipsum dolor sit amet, consectetur adipisicing eiusmod tempor incididunt ut labore
                                    et consectetur adipisicing eiusmod dolore mangna aliqua.
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="container pf-container">
                    <div className="row p-5 ">
                        {
                            Product_feature_data.map((fcard,findex)=>{
                                return <Featured_card key={findex} 
                                Product_image={fcard.Product_image}
                                Product_name={fcard.Product_name}
                                Product_price={fcard.Product_price}
                                Product_detail={fcard.Product_detail}
                                Product_love={fcard.Product_like}
                                Product_share={fcard.Product_share}/>
                            })
                        }
                    </div>
                </div>
            </section>
        </>
    )
}

export default Featured;
                                        
                                         
