import React from 'react';
import { NavLink } from 'react-router-dom';
import "./Button.css";

const Button =(props)=>{
    return(
        <button type="button" className="btn 0 btn-outline-dark pf-button mt-3 mx-2 mx-sm-2"  data-toggle="modal" data-target="#exampleModal">
            <NavLink to={props.link} >{props.name}</NavLink>
        </button>
    )
}

export default Button;